<template>
<Transition name="headerModal">
  <div class="header__modal">
    <div class="header__modal-block">
      <h3 class="header__modal-block-title">Buy now</h3>
      <div class="header__modal-block-items">
        <div class="header__modal-block-item" @click="product1 = !product1" :class="{active: product1}">
          <img src="@/assets/img/product1.png" alt="">
          <h3 class="header__modal-block-item-name">ice cream</h3>
        </div>
        <div class="header__modal-block-item" @click="product2 = !product2" :class="{active: product2}">
          <img src="@/assets/img/product2.png" alt="">
          <h3 class="header__modal-block-item-name">ice coffee</h3>
        </div>
        <div class="header__modal-block-item" @click="product3 = !product3" :class="{active: product3}">
          <img src="@/assets/img/product3.png" alt="">
          <h3 class="header__modal-block-item-name">milkshake</h3>
        </div>
      </div>
      <div class="header__modal-block-inputs">
        <input type="text" v-model="headerModalName" placeholder="Name" class="header__modal-block-inp">
        <input type="text" v-model="headerModalPhone" placeholder="Phone" class="header__modal-block-inp">
        <input type="text" placeholder="Comment" class="header__modal-block-inp">
      </div>
      <button class="header__modal-block-btn" @click="sendHeaderModal" >Submit</button>
      <button class="header__modal-block-close" @click="closeHeaderModal"><fa icon="fa-xmark"/></button>
      <span class="header__modal-block-error" v-show="submitError">Enter name and phone number*</span>
    </div>
  </div>
  </Transition>
</template>

<script>
export default {
  data(){
    return{
      product1: false,
      product2: false,
      product3: false,
      headerModalName: '',
      headerModalPhone: '',
      submitError: false
    }
  },
  methods:{
    sendHeaderModal(){
      if (this.headerModalName && this.headerModalPhone != '') {
        this.$emit('sendHeaderModal')
        this.headerModalName = ''
        this.headerModalPhone = ''
        this.submitError = false
      } else {
        this.submitError = true
      }
    },
    closeHeaderModal(){
      this.headerModalName = ''
      this.headerModalPhone = ''
      this.$emit('closeHeaderModal')
      this.submitError = false
    }
  }
}
</script>

